  get({ id, url, data }) {
    let result;

    if (data.url) {
      // workaround to replace duplicate slashes
      data.url = data.url.replace(/\/\//, "/");
      result = this.pages.find(n => pathToRegexp(n.url).test(data.url));
    }

    if (!result) {
      result = this.pages.find(n => n.url === "/page-not-found/");
    }

    return Promise.resolve(result);
  }
